html {
  font-family: PT Sans, Helvetica, Arial, sans-serif;
}

@media (min-width: 48em) {
  html {
    font-size: 16px;
  }
}

@media (min-width: 58em) {
  html {
    font-size: 20px;
  }
}

.sidebar {
  text-align: center;
  color: #ffffff80;
  background-color: #202020;
  padding: 2rem 1rem;
}

@media (min-width: 48em) {
  .sidebar {
    width: 18rem;
    text-align: left;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
  }
}

.sidebar a {
  color: #fff;
}

.sidebar-about h1 {
  color: #fff;
  margin-top: 0;
  font-family: Abril Fatface, serif;
  font-size: 3.25rem;
}

.sidebar-nav {
  padding-left: 0;
  list-style: none;
}

.sidebar-nav-item {
  display: block;
}

a.sidebar-nav-item:hover, a.sidebar-nav-item:focus {
  text-decoration: underline;
}

.sidebar-nav-item.active {
  font-weight: bold;
}

@media (min-width: 48em) {
  .sidebar-sticky {
    position: absolute;
    bottom: 1rem;
    left: 1rem;
    right: 1rem;
  }

  @supports (padding: max(0px)) {
    body:not(.layout-reverse) .sidebar-sticky {
      padding-left: max(1rem, env(safe-area-inset-left));
    }

    .layout-reverse .sidebar-sticky {
      padding-right: max(1rem, env(safe-area-inset-right));
    }
  }
}

.content {
  padding-top: 4rem;
  padding-bottom: 4rem;
}

@supports (padding: max(0px)) {
  body:not(.layout-reverse) .content {
    padding-right: max(1rem, env(safe-area-inset-right));
  }

  .layout-reverse .content {
    padding-left: max(1rem, env(safe-area-inset-left));
  }
}

@media (min-width: 48em) {
  .content {
    max-width: 38rem;
    margin-left: 20rem;
    margin-right: 2rem;
  }
}

@media (min-width: 64em) {
  .content {
    margin-left: 22rem;
    margin-right: 4rem;
  }
}

@media (min-width: 48em) {
  .layout-reverse .sidebar {
    left: auto;
    right: 0;
  }

  .layout-reverse .content {
    margin-left: 2rem;
    margin-right: 20rem;
  }
}

@media (min-width: 64em) {
  .layout-reverse .content {
    margin-left: 4rem;
    margin-right: 22rem;
  }
}

